@import '../../global.scss';

.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    align-items: center;

    h1{
        font-size: 50px;
        margin-top: 20px;
        @include mobile{
            font-size: 30px;      
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
        margin-top: 20px;
        @include mobile{
            flex-wrap: wrap;
            align-items: center; 
            justify-content: center;   
            margin: 0;   
        }
        

    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;     
        }
        .item{

            width: 400px;
            height:350px;
            border-radius: 20px;
            border: 1px solid lightgrey;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            position: relative;
            transition: all 0.5s ease;
            @include mobile{
                width: 130px;      
                height: 100px;
            }

            h3{
                position: absolute;
                font-size: 20px;
            }
            img{
               width: 100%;
               height: 100%;
               object-fit: cover;
               z-index: 1;
            }
            &:hover{
                background-color: blue;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}